
body {
  background-color: #d0d4db;
}
#search {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#link-back {
  color: white;
}
.center {
  margin-left: auto;
  margin-right: auto;
}
.card {
  height: 200px;
}
.card-block {
  background-color: #fcfc97;
}
.card-text {
  text-align: left;
  padding: 10px 30px;
}
.card-title {
  padding-top: 20px;
}
.heading {
  margin-bottom: 20px;
}
.inline {
  display: inline-block;
}
footer {
  margin: 40px;
}



